.nav {
    position: fixed;
    top: 0;
    display: flex;
    padding: 20px;
    justify-content: space-between;
    height: 30px;
    z-index: 1;
    transition-timing-function: ease-in;
    transition: all 0.5s;

}

.nav_black {
    background-color: #111;
    width: 100%;
    height: 50px;


}

.nav_logo {
    width: 100px;
    object-fit: contain;
    position: fixed;
    left: 20;
    font-weight: bold;
    top: 0;





}





.nav_avtar {
    width: 30px;
    object-fit: contain;
    right: 20px;
    width: 30px;
    position: fixed;
}